/* General Styles */
.container {
  font-family: Arial, sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
  background-color: #f7f9fc;
}

.body {
  width: 100%;
  max-width: 600px;
  background: #ffffff;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
}

/* Flexbox for input and button on larger screens */
.buttons {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
}

input[type=text] {
  padding: 8px 12px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 8px;
  outline: none;
  transition: border-color 0.3s;
  flex: 1;
}

input[type=text]:focus {
  border-color: #5b9bd5;
}

button {
  padding: 10px 16px;
  background-color: #5b9bd5;
  color: #ffffff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #4a8ac0;
}

.table {
  width: 100%;
}

table {
  width: 100%;
  margin-top: 20px;
}

th,
td {
  padding: 12px;
  text-align: left;
}

thead {
  background-color: #5b9bd5;
  color: white;
}

tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

tbody tr:nth-child(4) {
  border-bottom: 1px solid #ddd;
}

tbody tr:hover {
  background-color: #f1f7fc;
}

/* Responsive Adjustments */
@media (max-width: 600px) {
  .container {
    align-items: start;
    padding-bottom: 70px;
  }
  .body {
    width: 90%;
    padding: 15px;
    height: 100%;
  }
  /* Stack button and input fields vertically */
  .buttons {
    padding-right: 20px;
    height: 30;
    background-color: #f7f9fc;
    position: fixed;
    bottom: 0;
    flex-direction: row;
    gap: 5px;
  }
  .buttons .add {
    padding: 8px 10px;
    font-size: 16px;
    transition: background-color 0.3s;
    width: 50px;
  }
  .buttons .reset {
    padding: 8px 10px;
    font-size: 16px;
    transition: background-color 0.3s;
    width: 50px;
  }
  .buttons input {
    width: 100%;
  }
  .buttons .reset {
    width: 10px;
    display: none;
  }
  th,
  td {
    padding: 8px;
  }
}/*# sourceMappingURL=App.css.map */